ul,ol,li {
    list-style:none;
}

* {
    margin:0; padding:0;
}

body {
    background:#f3f3f3;
}

.loader {
    position:fixed;
    background:rgba(0,0,0,0.8);
    display:flex;
    align-items:center;
    justify-content:center;
    width:100vw;
    height:100vh;
    color:#fff;
    letter-spacing:2px;
}

.movieWrap {
    display:flex;
    justify-content:space-between;
    flex-wrap:wrap;
    margin:30px;
}

.movieWrap li {
    width:calc(50% - 10px);
    background:#fff;
    box-sizing:border-box;
    margin-bottom:20px;
    text-align:center;
    padding:30px;
}

.movie__title {
    margin:20px 0 10px;
}

.movie__summary {
    font-size:12px;
    color:#999;
}